import React from "react"
import { Button } from "reactstrap"
import styled from "styled-components"
import { fnListPopover } from "src/helpers/fnList"
import i18next from "i18next"

const Wrap = styled.div`
  padding: 0.5rem;
  max-width: ${props => props.maxWidth || "90vw"};
  & .a-filters {
    display: flex;
    flex-direction: row;
    // max-height: calc(100vh - 85px);
    // overflow: auto;
    // overflow-x: hidden;
  }
  & .a-footer {
    display: flex;
    flex-direction: row;
  }
`
export default function MUIATFilter(props) {
  const { cfp, withoutPopover } = props
  const filterList = cfp.onePage.getFilterList()
  const _manualFilterOuts = cfp.onePage._manualFilterOuts
  if (filterList && _manualFilterOuts && _manualFilterOuts.length > 0) {
    for (let i = 0; i < _manualFilterOuts.length; i++) {
      let _outOne = _manualFilterOuts[i]
      for (let j = 0; j < filterList.length; j++) {
        let _itemControl = filterList[j]
        if (_itemControl.fieldName === _outOne.fieldName) {
          _itemControl.currentValue = _outOne.value
          _itemControl.currentFilter = _outOne
        }
      }
    }
  }

  let _maxWith = "90vw"
  let _eAppMainInner = document.getElementsByClassName("app-main__inner")
  if (_eAppMainInner && _eAppMainInner.length > 0) {
    _maxWith = `${_eAppMainInner[0].offsetWidth - 60}px`
  }

  // console.warn("filterList:", filterList, _manualFilterOuts)
  return (
    <Wrap className="mpat-admintable_filter_popover" maxWidth={_maxWith}>
      <div>
        {/* <MyLib
          type="select2"
          value={[
            {
              Value: "1",
              Text: "11",
            },
          ]}
          options={[
            {
              Value: "1",
              Text: "11",
            },
            { Value: "2", Text: "22" },
          ]}
        /> */}
      </div>
      <div className="a-filters row">
        {filterList &&
          filterList.length > 0 &&
          filterList.map((v, i) => {
            return v.render({
              key: i,
              fnList: {
                ...cfp.fnList,
                onSubmitFilter: () => {
                  cfp.handleFilter()
                  fnListPopover.hidePopover(props)
                },
              },
            })
          })}
      </div>
      <div className="a-footer">
        <Button
          color="danger"
          outline
          onClick={() => {
            console.warn("click clear filter", cfp.onePage)
            cfp.handleClearFilter()
            fnListPopover.hidePopover(props)
          }}
          style={{}}
        >
          {i18next.t("Clear All Filter")}
        </Button>
        <div style={{ marginLeft: "auto", marginRight: "10px" }}>
          {withoutPopover !== true && (
            <Button
              color="secondary"
              style={{ marginLeft: "auto", marginRight: "10px" }}
              onClick={() => {
                fnListPopover.hidePopover(props)
              }}
            >
              {i18next.t("Close")}
            </Button>
          )}

          <Button
            color="primary"
            style={{}}
            onClick={() => {
              console.warn("click filter", cfp.onePage)
              cfp.handleFilter()
              fnListPopover.hidePopover(props)
            }}
          >
            {i18next.t("Filter")}
          </Button>
        </div>
      </div>
    </Wrap>
  )
}
